:root {
    --Light-Cyan-Cabecera: #14B38B;
    --Light-Cyan-bxsh: #0b4b3c;

    --Light-Roxa-Cabecera: #A30046;
    --Light-Roxa-bxsh: #3f0b22;
}

.box{
    position: fixed;
    display: flex;
    height: 5em;
    width: 100%;
    flex-wrap: nowrap;
    transition: all 2.4s;
    opacity: .98;
    z-index: 20;
}
.box.Light-Cyan{
    background-color: var(--Light-Cyan-Cabecera);
    box-shadow: 1px 1px 4px 2px var(--Light-Cyan-bsh);
}

.box.Light-Cyan input.button{
    box-shadow: 0px 0px 3px 0px var(--Light-Cyan-bxsh);
}


.box.Light-Roxa{
    background-color: var(--Light-Roxa-bsh);
    box-shadow: 1px 1px 4px 2px var(--Light-Roxa-bxsh);
}
.box.Light-Roxa input.button{
    box-shadow: 0px 0px 3px 0px var(--Light-Roxa-bxsh);
}

.box--inicio{
    margin-top: .3em;
    position: absolute;
    width: 28%;
    height: 90%;
    transition: all 2.5s;
}
.box--inicio:hover{
    margin-top: 3px;
    transform: scale(1.22);
}
.box--inicio:hover+.box--tools{
    height: 105%;
}

.box--inicio>img{
    margin: 0 auto;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}

.box--tools{
    margin-top: .3em;
    position: absolute;
    display: inline-flex;
    width: 38%;
    height: 90%;
    right: 0;
    transition: all 2s;
}
.box--tools>img{
    position: absolute;
    height: 100%;
    width: 48%;
    object-fit: fill;
}
.box--tools>.almacen{
    left: 0;
}
.box--tools>.ajustes{
    width: 50%;
    right: -.6em;
}

/*PC*/
@media only screen and (min-width: 667px){
    .box--inicio{
        width: 20%;
    }
    .box--tools{
        width: 26%;
    }

    .box--inicio>img{
        margin-top: -4px;
        margin-left: 1em;
        width: 70%;
        height: 120%;
        object-fit: fill;
    }

    .box--tools>img{
        width: 40%;
    }
    .box--tools>.ajustes{
        right: -1em;
    }
}


