.box input.button {
    position: absolute;
    color: transparent;
    background-color: transparent;
    border: 0;

    outline: 0;
    height: 100%;
    border-radius: .3em;
}

.box--inicio>.init{
    position: absolute;
    width: 110%;
}
.box--tools>.bAlm{
    width: 52%;
    left: 0;
}
.box--tools>.bAj{
    width: 40%;
    right: 0;
}