:root {
    --Light-Cyan-aside1: #188F71;
    --Light-Cyan-aside2: #188F71;
    --Light-Cyan-texto: #b4e3d8;

    --Light-Roxa-aside1: #730034;
    --Light-Roxa-aside2: #CB0057;
    --Light-Roxa-texto: #e8a7c4;
}

div.i {
    position: fixed;
    display: inline-flex;
    flex-wrap: nowrap;
    flex-direction: column;
    right: 0;
    width: 3em;
    height: 3em;
    margin-top: 8em;
    background-color: blue;
    z-index: 10;
    border-radius: .6em;
    transition: all 2s;
    transition-delay: 1s;
    opacity: .8;
}

div.i:hover{
    width: 20vh;
    transition-delay: .5s;
    opacity: .9;
}

div.i>img{
    position: absolute;
    width: 3em;
    height: 3em;
    object-fit: fill;
    transition: all .5s;
    transition-delay: 1s;
}

div.i:hover>img{
    transform: scale(0);
}

div.i>a{
    position: relative;
    display: inline-block;
    transform: scale(0);
    transition: all .5s;
    transition-delay: 1s;
}

div.i:hover>a{
    transform: scale(1);
}