:root {
    --Light-Cyan-articulo: #14B38B;
    --Light-Cyan-bkground: #90CCBE;
    --Light-Cyan-bxsh: #0b4b3c;
    --Light-Cyan-texto: #b4e3d8;

    --Light-Roxa-articulo: #CB0057;
    --Light-Roxa-bkground: #FA026D;
    --Light-Roxa-bxsh: #3f0b22;
    --Light-Roxa-texto: #e8a7c4;
}

*{
    margin: 0;
}

div.index.Light-Cyan {
    background-color: var(--Light-Cyan-bkground);
}

div.index.Light-Roxa {
    background-color: var(--Light-Roxa-bkground);
}

div.index.Light-Cyan>div.ArticleMain {
    background-color: var(--Light-Cyan-articulo);
    border-radius: 1em;
    color: var(--Light-Cyan-texto);
}

.ArticleMain {
    position: relative;
    display: inline-flex;
    padding: .6em;
    margin-top: 5.4em;
    margin-left: .4em;
    margin-right: .4em;
}

@media only screen and (max-width: 667px) {
    .ArticleMain {
        max-width: 94%;
    }
}

/*pc*/
@media only screen and (min-width: 667px) {
    .ArticleMain {
        max-width: 88%;
    }
}